import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Col, Row, Space, Button, Table, Image, Drawer, Typography, Collapse, Popconfirm, Switch } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'
import AlertWait from '../../pages/AlertWait'
import { getCatalogos, getDatosUsuario, toCapitalize } from '../../utils/Utils'

import PostulantService from '../../service/PostulantService'
import BeneficiaryService from './../../service/BeneficiaryService'
import Beneficiary from './Beneficiary'

const postulantService = new PostulantService()
const beneficiaryService = new BeneficiaryService()

const { Title } = Typography

const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
        position: ['none', 'bottomRight']
    }
}
const panelStyle = {
    marginBottom: 20,
    backgroundColor: '#fafafa',
    border: 'none',
    borderRadius: '15px'
}

const Beneficiaries = () => {
    const navigate = useHistory()
    const { operation } = useParams()
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isLoadingBeneficiaries, setIsLoadingBeneficiaries] = useState(false)
    const [isSaving, setIsSaving] = useState(false)
    const [isDeleting, setIsDeleting] = useState(false)
    const [isSending, setIsSending] = useState(false)
    const [postulant, setPostulant] = useState({})
    const [beneficiaryC1, setBeneficiaryC1] = useState(null)
    const [beneficiaryC2, setBeneficiaryC2] = useState(null)
    const [openC1, setOpenC1] = useState(false)
    const [openC2, setOpenC2] = useState(false)

    const closeBeneficiaryC1 = () => {
        setOpenC1(false)
    }

    const getBeneficiaryC1 = async ({ benId }) => {
        setBeneficiaryC1(null)
        setIsLoadingBeneficiaries(true)
        try {
            const { data } = await beneficiaryService.get({ navigate, beneficiary: { benId } })
            const beneficiary = {
                ...data.listObject[0],
                idTipo: data.listObject[0].benTipo.catId,
                idPais: data.listObject[0].benPais.catId,
                idSexo: data.listObject[0].benSexo.catId,
                idNivelInt: data.listObject[0].benNivelInt.catId,
            }
            setBeneficiaryC1(beneficiary)
            setOpenC1(true)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsLoadingBeneficiaries(false)
        }
    }

    const saveBeneficiaryC1 = async (values) => {
        setIsSaving(true)
        const beneficiary = { ...values }
        try {
            const { data } = (beneficiary.benId !== undefined ? await beneficiaryService.set({ navigate, beneficiary }) : await beneficiaryService.put({ navigate, beneficiary }))
            if (data) fetchData(2)
            closeBeneficiaryC1()
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsSaving(false)
        }
    }

    const closeBeneficiaryC2 = () => {
        setOpenC2(false)
    }

    const getBeneficiaryC2 = async ({ benId }) => {
        setBeneficiaryC2(null)
        setIsLoadingBeneficiaries(true)
        try {
            const { data } = await beneficiaryService.get({ navigate, beneficiary: { benId } })
            const beneficiary = {
                ...data.listObject[0],
                idTipo: data.listObject[0].benTipo.catId,
                idPais: data.listObject[0].benPais.catId,
                idSexo: data.listObject[0].benSexo.catId,
            }
            setBeneficiaryC2(beneficiary)
            setOpenC2(true)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsLoadingBeneficiaries(false)
        }
    }

    const saveBeneficiaryC2 = async (values) => {
        setIsSaving(true)
        const beneficiary = { ...values }
        try {
            const { data } = (beneficiary.benId !== undefined ? await beneficiaryService.set({ navigate, beneficiary }) : await beneficiaryService.put({ navigate, beneficiary }))
            if (data) fetchData(2)
            closeBeneficiaryC2()
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsSaving(false)
        }
    }

    const delBeneficiary = async ({ benId }) => {
        setIsDeleting(true)
        try {
            const { data } = await beneficiaryService.del({ navigate, beneficiary: { benId } })
            if (data) {
                toast.success('Beneficiary successfully removed')
                fetchData(2)
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsDeleting(false)
        }
    }

    const validateBeneficiary = async (values) => {
        setIsSaving(true)
        const beneficiary = { ...values }
        try {
            const { data } = await beneficiaryService.val({ navigate, beneficiary })
            if (data)
                toast.success('Beneficiary successfully updated')
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsSaving(false)
        }
    }

    const sendBeneficiaries = async () => {
        setIsSending(true)
    }

    const renderCourse = ({ curId, curNombre }, posId) =>
        <Collapse.Panel
            key={curId}
            header={
                <Row gutter={10}>
                    <Col>
                        <Typography.Text strong style={{ fontSize: '1.1em' }}>
                            {curNombre.catNombre}
                        </Typography.Text>
                    </Col>
                </Row>
            }
            style={panelStyle}
        >
            <Row>
                <Col span={24}
                    style={{ textAlign: 'right' }}>
                    <Space>
                        <Button
                            type='default'
                            loading={isLoading || isLoadingBeneficiaries}
                            disabled={isLoading || isLoadingBeneficiaries || isSending}
                            style={{ width: '130px' }}
                            onClick={() => {
                                fetchData(2)
                            }}>Refresh</Button>
                        {
                            operation === 'registry' &&
                            <Button
                                type='primary'
                                style={{ width: '130px' }}
                                disabled={isLoading || isLoadingBeneficiaries}
                                onClick={() => {
                                    if (curNombre.catValor === 'CUR_NEG_VER1') {
                                        closeBeneficiaryC1()
                                        const ben = {
                                            idCurso: curId,
                                            idPostulante: posId,
                                            benTrabaja: false,
                                            idTipo: getCatalogos().typePersons.filter(t => t.catValor === (curNombre.catReferencia.catValor || 'TYP_PER_NAT'))[0].catId
                                        }
                                        setBeneficiaryC1(ben)
                                        setOpenC1(true)
                                    } else if (curNombre.catValor === 'CUR_NEG_VER2') {
                                        closeBeneficiaryC2()
                                        const ben = {
                                            idCurso: curId,
                                            idPostulante: posId,
                                            idTipo: getCatalogos().typePersons.filter(t => t.catValor === (curNombre.catReferencia.catValor || 'TYP_PER_LEG'))[0].catId
                                        }
                                        setBeneficiaryC2(ben)
                                        setOpenC2(true)
                                    }
                                }}>New Beneficiary</Button>
                        }
                        {
                            operation === 'qualification' &&
                            <Popconfirm title="Are you sure you want to send the list of beneficiaries?" okText="Yes, send" cancelText="No" onConfirm={() => sendBeneficiaries()}
                                okButtonProps={{
                                    loading: isSending,
                                    disabled: isSending || isLoading || isLoadingBeneficiaries
                                }}
                            >
                                <Button type='primary'
                                    style={{ width: '130px' }}
                                    loading={isSending}
                                    disabled={isSending || isLoading || isLoadingBeneficiaries}
                                >
                                    {
                                        isSending ? 'Sending' : 'Send'
                                    }
                                </Button>
                            </Popconfirm>
                        }
                    </Space>
                </Col>
            </Row>
            {
                postulant && postulant.beneficiarioList &&
                <Table
                    loading={isLoading || isLoadingBeneficiaries}
                    {...tableProps}
                    rowKey='benId'
                    columns={[
                        {
                            title: '',
                            dataIndex: 'benId',
                            render: (_, { benId }) => <Image key={benId} preview={false} alt='' src='./assets/user.png' width={32} height={32} />
                        },
                        {
                            title: 'ID Number',
                            dataIndex: 'benDocumento',
                            render: (_, { benDocumento }) => `${benDocumento}`,
                            responsive: ['md', 'lg', 'xl', 'xxl']
                        },
                        {
                            title: 'Name',
                            dataIndex: 'benNombres',
                            render: (_, { benNombres }) => `${benNombres}`
                        },
                        {
                            title: 'Country',
                            dataIndex: 'benPais',
                            render: (_, { benPais }) =>
                                <div style={{ textAlign: 'center' }}>
                                    <img
                                        src={`https://flagcdn.com/32x24/${benPais.catDescripcion.toLowerCase()}.png`}
                                        srcSet={`https://flagcdn.com/32x24/${benPais.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/96x72/${benPais.catDescripcion.toLowerCase()}.png 3x`}
                                        width="32"
                                        height="24"
                                        alt={benPais.catNombre}></img>
                                </div>
                        },
                        {
                            title: 'City',
                            dataIndex: 'ciudad',
                            render: (_, { ciudad }) => `${ciudad}`,
                            responsive: ['sm', 'md', 'lg', 'xl', 'xxl']
                        },
                        (
                            curNombre.catReferencia.catValor === 'TYP_PER_NAT' ?
                                {
                                    title: 'Sex',
                                    dataIndex: 'benSexo',
                                    render: (_, { benSexo }) => `${benSexo.catNombre}`,
                                    responsive: ['xl', 'xxl']
                                } :
                                {
                                    title: 'Job Title',
                                    dataIndex: 'benCargo',
                                    render: (_, { benCargo }) => `${benCargo}`,
                                    responsive: ['xl', 'xxl']
                                }
                        ),
                        (
                            curNombre.catReferencia.catValor === 'TYP_PER_NAT' ?
                                {
                                    title: 'Is Working',
                                    dataIndex: 'benTrabaja',
                                    render: (_, { benTrabaja }) => <>{benTrabaja ? 'Yes' : 'No'}</>,
                                    responsive: ['lg', 'xl', 'xxl']
                                } :
                                {
                                    title: 'Company',
                                    dataIndex: 'benNomEmpresa',
                                    render: (_, { benNomEmpresa }) => `${benNomEmpresa}`,
                                    responsive: ['lg', 'xl', 'xxl']
                                }
                        ),
                        (
                            curNombre.catReferencia.catValor === 'TYP_PER_NAT' ?
                                {
                                    title: 'Ins. Level',
                                    dataIndex: 'benNivelInt',
                                    render: (_, { benNivelInt }) => `${benNivelInt.catNombre}`,
                                    responsive: ['lg', 'xl', 'xxl']
                                } :
                                {
                                    title: 'Total Persons',
                                    dataIndex: 'benNumPer',
                                    render: (_, { benNumPer }) => `${benNumPer}`,
                                    responsive: ['lg', 'xl', 'xxl']
                                }
                        ),
                        {
                            title: 'Actions',
                            dataIndex: '',
                            render: (_, record) => <div style={{ textAlign: 'center' }}>
                                <Space>
                                    {
                                        operation === 'registry' &&
                                        <>
                                            <Button type='primary'
                                                icon={<anticons.FolderOpenFilled />}
                                                onClick={() => {
                                                    if (curNombre.catValor === 'CUR_NEG_VER1') {
                                                        getBeneficiaryC1(record)
                                                    } else if (curNombre.catValor === 'CUR_NEG_VER2') {
                                                        getBeneficiaryC2(record)
                                                    }
                                                }}
                                            />
                                            <Popconfirm title="Are you sure you want to delete the beneficiary?" okText="Yes, delete" cancelText="No" onConfirm={() => delBeneficiary(record)}
                                                okButtonProps={{
                                                    loading: isDeleting,
                                                    disabled: isDeleting
                                                }}
                                            >
                                                <Button type='default'
                                                    danger
                                                    icon={<anticons.DeleteFilled />}
                                                    loading={isDeleting}
                                                    disabled={isDeleting}
                                                />
                                            </Popconfirm>
                                        </>
                                    }
                                    {
                                        operation === 'qualification' &&
                                        <Switch
                                            defaultChecked={record.benValidado}
                                            onChange={(e) => {
                                                validateBeneficiary({ ...record, benValidado: e })
                                            }}
                                        />
                                    }
                                </Space>
                            </div>
                        }
                    ]}
                    dataSource={postulant.beneficiarioList.filter(b => b.idCurso === curId)}>
                </Table>
            }
        </Collapse.Panel>

    const fetchData = async (typeLoad) => {
        setLoad(false)
        if (typeLoad) {
            if (typeLoad === 1) {
                setIsLoading(true)
            } else if (typeLoad === 2) {
                setIsLoadingBeneficiaries(true)
            }
        } else {
            setIsLoading(true)
        }
        try {
            const postulant = { idUsuario: getDatosUsuario().usuarioId }
            const { data } = await postulantService.getTraining({ navigate, postulant })
            const training = data.listObject[0]
            setPostulant(training)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        } finally {
            if (typeLoad) {
                if (typeLoad === 1) {
                    setIsLoading(false)
                } else if (typeLoad === 2) {
                    setIsLoadingBeneficiaries(false)
                }
            } else {
                setIsLoading(false)
            }
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    return (
        <>
            <div style={
                {
                    backgroundColor: '#ffffff',
                    padding: '15px 20px 6px 20px',
                    borderRadius: '15px',
                    marginBottom: '20px'
                }
            }>
                <Row>
                    <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                        <Typography>
                            <Title type='secondary' level={3}>
                                Beneficiaries {` ${toCapitalize(operation)}`}
                            </Title>
                        </Typography>
                    </Col>
                </Row>
            </div>
            <Row justify='center'>
                {
                    isLoading ?
                        <AlertWait message='Loading...' isLoading={isLoading} />
                        :
                        <Col span={24}>
                            <Collapse
                                accordion
                                bordered={false}
                                expandIconPosition='end'
                                style={{
                                    backgroundColor: 'transparent'
                                }}
                            >
                                {
                                    postulant.entId &&
                                    postulant.entId.cursoList &&
                                    postulant.entId.cursoList
                                        .filter(c => c.curId === postulant.informeList.filter(w =>
                                            w.infTipo.catValor === 'TYP_INF_WPL' &&
                                            w.infEstado.catValor === 'EST_INF_APR' &&
                                            w.idCurso === c.curId
                                        )[0].idCurso)
                                        .map(c => renderCourse(c, postulant.posId))
                                }
                            </Collapse>
                        </Col>
                }
            </Row>
            <Drawer title="Beneficiary" placement="right" onClose={closeBeneficiaryC1} open={openC1}
                extra={
                    <Space>
                        <Button onClick={closeBeneficiaryC1}
                            disabled={isSaving}
                        >Cancel</Button>
                    </Space>
                }
            >
                {
                    beneficiaryC1 && <Beneficiary type={1} beneficiary={beneficiaryC1} saveBeneficiary={saveBeneficiaryC1} closeBeneficiary={closeBeneficiaryC1} isSaving={isSaving} />
                }
            </Drawer >
            <Drawer title="Beneficiary" placement="right" onClose={closeBeneficiaryC2} open={openC2}
                extra={
                    <Space>
                        <Button onClick={closeBeneficiaryC2}
                            disabled={isSaving}
                        >Cancel</Button>
                    </Space>
                }
            >
                {
                    beneficiaryC2 && <Beneficiary type={2} beneficiary={beneficiaryC2} saveBeneficiary={saveBeneficiaryC2} closeBeneficiary={closeBeneficiaryC2} isSaving={isSaving} />
                }
            </Drawer>
        </>
    )
}

export default Beneficiaries